<template>
  <section>
    <div class="hotsong-top">
      <img src="https://s3.music.126.net/mobile-new/img/hot_music_bg_2x.jpg?f01a252389c26bcf016816242eaa6aee=" alt="">
      <div class="hotopct">
      <div class="hoticon"></div>
      <div class="hottime">
        更新日期：{{date}}
      </div>
      </div>
    </div>
    <ul>
      <MusicListItem
        @change-current-play-song="
        $emit('change-current-play-song', $event)
        $emit('change-current-play-song-list',hotSongs)
        "
        v-for="(item, index) in hotSongs"
        :key="item.id"
        :item="item"
        :index="index"
      />
    </ul>
  </section>
</template>

<script>
import MusicListItem from "../components/MusicListItem.vue";
export default {
  components: {
    MusicListItem,
  },
  data: function () {
    return {
      hotSongs: [],
      month:Number,
      day:Number,
      date:String,
    };
  },
  created() {
    this.axios.get("https://music.kele8.cn/top/list?idx=1").then((res) => {
      this.hotSongs = res.data.playlist.tracks;
    });
  },
  mounted(){
   var myDate = new Date();
   this.date =  (( myDate.getMonth() +1)<10 ?"0" +( myDate.getMonth() +1):( myDate.getMonth() +1))+ "月" +  myDate.getDate() + '日'; 
  } 
};
</script>

<style lang="less" >
.hotsong-top{
  position: relative;
  top:0;
  left: 0;
  z-index: -2;
  img{
    width: 100%;
  }
  .hotopct{
    display: flex;
    flex-direction: column;
    -webkit-box-pack: center;
    justify-content: center;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 2;
    padding-left: 20px;
    box-sizing: border-box;
  }
  .hoticon{
    width: 142px;
    height: 67px;
    background: url(../assets/index_icon_2x.png) no-repeat;
    background-position: -24px -30px;
    background-size: 166px 97px;
  }
  .hottime{
    margin-top: 10px;
    color: hsla(0,0%,100%,.8);
    font-size: 12px;
  }
}
.hotsong-top::after{
  content: " ";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    background-color: rgba(0,0,0,.2);
}
</style>